import { AfterViewInit, Directive, Input, OnChanges, OnInit } from '@angular/core';
import { Dataviz } from 'keen-dataviz';

@Directive({
  selector: '[keenApiData]'
})
export class KeenApiDataDirective implements OnChanges, OnInit, AfterViewInit {
  viewInit = false;
  @Input() keenApiInput: any;
  chart;

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges() {
    if (this.viewInit) {
      this.doQuery();
    }
  }

  doQuery() {
    this.chart = new Dataviz({
      container: `.${this.keenApiInput.classSelector}`,
      type: this.keenApiInput.viewType,
      axis: {
        x: {
          label: this.keenApiInput.xAxisLabel,
        },
        y: {
          label: this.keenApiInput.yAxisLabel,
        }
      },
      legend: {
        pagination: {
          limit: 10
        },
        label: {
          textMaxLength: 25
        }
      }
    });

    this.chart.render({
      result: this.keenApiInput.data
    });
  }

  ngAfterViewInit() {
    this.viewInit = true;
    this.doQuery();
  }
}
